"use client";

import { useState } from "react";
import { Copy, LogOut } from "lucide-react";
import { useAccount, useDisconnect } from "@starknet-react/core";
import { WalletConnectorModal } from "../components/wallet";
import { getShortAddress } from "../utils/getShortAddress";
import { useBalance } from "@starknet-react/core";
import { Skeleton } from "@heroui/react";

interface GetBalanceProps {
  address?: string | null;
}

export function GetBalance({ address }: GetBalanceProps) {
  const { data, isLoading, isError, error } = useBalance({
    address: (address as `0x${string}`) || undefined,
  });

  if (isError)
    return (
      <div className="flex items-center gap-2 text-red-500 text-sm">
        Error fetching balance
      </div>
    );

  return (
    <>
      <div className="text-gray-600 text-sm mb-2">Balance</div>
      <div className="text-2xl font-semibold flex items-center justify-center mb-4">
        {isLoading ? (
          <Skeleton className="w-24 h-6" />
        ) : (
          <div className="flex items-center gap-2">
            {data?.formatted || "0.00"}
            <div>{data?.symbol || "ETH"}</div>
          </div>
        )}
      </div>
    </>
  );
}

interface DepositFormProps {
  amount: string;
  setAmount: (amount: string) => void;
  onConfirm: () => void;
}

function DepositForm({ amount, setAmount, onConfirm }: DepositFormProps) {
  const [amountError, setAmountError] = useState("");

  const handleAmountChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value;

    // Validate amount
    if (value && parseFloat(value) <= 0) {
      setAmountError("Amount must be greater than 0");
    } else if (value && isNaN(parseFloat(value))) {
      setAmountError("Amount must be a valid number");
    } else {
      setAmountError("");
    }

    setAmount(value);
  };

  const handleConfirm = () => {
    // Additional validation before confirming
    if (!amount) {
      setAmountError("Amount is required");
      return;
    }
    if (parseFloat(amount) <= 0) {
      setAmountError("Amount must be greater than 0");
      return;
    }
    if (isNaN(parseFloat(amount))) {
      setAmountError("Amount must be a valid number");
      return;
    }

    setAmountError("");
    onConfirm();
  };

  return (
    <>
      {/* Amount Input */}
      <div className="mb-6">
        <label className="block text-gray-600 text-sm mb-2">Amount</label>
        <input
          type="number"
          value={amount}
          onChange={handleAmountChange}
          min="0"
          step="any"
          className={`w-full py-4 px-6 text-lg border-2 rounded-xl focus:outline-none transition-colors ${
            amountError
              ? "border-red-500 focus:border-red-500"
              : "border-gray-300 focus:border-blue-500"
          }`}
          placeholder="Enter amount"
        />
        {amountError && (
          <p className="text-red-500 text-sm mt-2">{amountError}</p>
        )}
      </div>

      {/* Confirm Button */}
      <button
        onClick={handleConfirm}
        disabled={!!amountError || !amount || parseFloat(amount) <= 0}
        className={`w-auto px-8 py-3 rounded-xl font-medium transition-colors float-right ${
          amountError || !amount || parseFloat(amount) <= 0
            ? "bg-gray-400 text-gray-200 cursor-not-allowed"
            : "bg-gray-800 text-white hover:bg-gray-700"
        }`}
      >
        Confirm
      </button>
      <div className="clear-both"></div>
    </>
  );
}

interface WithdrawFormProps {
  recipient: string;
  setRecipient: (recipient: string) => void;
  amount: string;
  setAmount: (amount: string) => void;
  onConfirm: () => void;
}

function WithdrawForm({
  recipient,
  setRecipient,
  amount,
  setAmount,
  onConfirm,
}: WithdrawFormProps) {
  const [recipientError, setRecipientError] = useState("");

  const handleRecipientChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value;

    if (value && !value.startsWith("0x")) {
      setRecipientError("Recipient address must start with 0x");
    } else {
      setRecipientError("");
    }

    setRecipient(value);
  };

  const handleConfirm = () => {
    if (!recipient.startsWith("0x")) {
      setRecipientError("Recipient address must start with 0x");
      return;
    }
    if (!amount || parseFloat(amount) <= 0) {
      return;
    }
    setRecipientError("");
    onConfirm();
  };

  return (
    <>
      <div className="mb-6">
        <label className="block text-gray-600 text-sm mb-2">
          Recipient address
        </label>
        <input
          type="text"
          value={recipient}
          onChange={handleRecipientChange}
          className={`w-full py-4 px-6 text-lg border-2 rounded-xl focus:outline-none transition-colors ${
            recipientError
              ? "border-red-500 focus:border-red-500"
              : "border-gray-300 focus:border-blue-500"
          }`}
          placeholder="0x..."
        />
        {recipientError && (
          <p className="text-red-500 text-sm mt-2">{recipientError}</p>
        )}
      </div>

      {/* Amount Input */}
      <div className="mb-6">
        <label className="block text-gray-600 text-sm mb-2">Amount</label>
        <input
          type="number"
          value={amount}
          onChange={(e) => setAmount(e.target.value)}
          className="w-full py-4 px-6 text-lg border-2 border-gray-300 rounded-xl focus:outline-none focus:border-blue-500 transition-colors"
          placeholder="Enter amount"
        />
      </div>

      {/* Confirm Button */}
      <button
        onClick={handleConfirm}
        disabled={
          !!recipientError || !recipient || !amount || parseFloat(amount) <= 0
        }
        className={`w-auto px-8 py-3 rounded-xl font-medium transition-colors float-right ${
          recipientError || !recipient || !amount || parseFloat(amount) <= 0
            ? "bg-gray-400 text-gray-200 cursor-not-allowed"
            : "bg-gray-800 text-white hover:bg-gray-700"
        }`}
      >
        Confirm
      </button>
      <div className="clear-both"></div>
    </>
  );
}

export default function MainForm() {
  const { address } = useAccount();
  const { disconnect } = useDisconnect();

  const [amount, setAmount] = useState("1");
  const [recipient, setRecipient] = useState("");
  const [activeTab, setActiveTab] = useState("deposit");

  const handleCopy = () => {
    navigator.clipboard.writeText(address!);
  };

  const handleLogout = () => {
    disconnect();
  };

  const handleConfirm = () => {
    if (activeTab === "deposit") {
      console.log(`Depositing ${amount}`);
    } else {
      console.log(`Withdrawing ${amount} to ${recipient}`);
    }
  };

  if (!address)
    return (
      <div className="flex items-center justify-center min-h-screen bg-gray-50">
        <WalletConnectorModal />
      </div>
    );

  return (
    <div className="min-h-screen bg-gray-50 flex items-center justify-center p-4">
      <div className="bg-white rounded-3xl shadow-lg p-8 w-full max-w-md">
        {/* Header with address and buttons */}
        <div className="flex items-center justify-between mb-8">
          <div className="flex-1">
            <span className="text-gray-600 text-sm">address</span>
            <div className="font-mono text-sm mt-1">
              {getShortAddress(address)}
            </div>
          </div>
          <div className="flex gap-2 ml-4">
            <button
              onClick={handleCopy}
              className="p-2 border-2 border-gray-300 rounded-lg hover:bg-gray-50 transition-colors cursor-pointer"
              title="Copy address"
            >
              <Copy size={16} />
            </button>
            <button
              onClick={handleLogout}
              className="p-2 border-2 border-gray-300 rounded-lg hover:bg-gray-50 transition-colors cursor-pointer"
              title="Logout"
            >
              <LogOut size={16} />
            </button>
          </div>
        </div>

        <div className="text-center mb-8">
          <GetBalance address={address} />
        </div>

        {/* Deposit/Withdraw Tabs */}
        <div className="flex gap-2 mb-6">
          <button
            onClick={() => setActiveTab("deposit")}
            className={`flex-1 py-3 px-6 rounded-xl font-medium transition-colors cursor-pointer ${
              activeTab === "deposit"
                ? "bg-red-500 text-white border-2 border-red-500"
                : "bg-white text-gray-700 border-2 border-gray-300 hover:bg-gray-50"
            }`}
          >
            Deposit
          </button>
          <button
            onClick={() => setActiveTab("withdraw")}
            className={`flex-1 py-3 px-6 rounded-xl font-medium transition-colors cursor-pointer ${
              activeTab === "withdraw"
                ? "bg-gray-800 text-white border-2 border-gray-800"
                : "bg-white text-gray-700 border-2 border-gray-300 hover:bg-gray-50"
            }`}
          >
            Withdraw
          </button>
        </div>

        {activeTab === "deposit" ? (
          <DepositForm
            amount={amount}
            setAmount={setAmount}
            onConfirm={handleConfirm}
          />
        ) : (
          <WithdrawForm
            recipient={recipient}
            setRecipient={setRecipient}
            amount={amount}
            setAmount={setAmount}
            onConfirm={handleConfirm}
          />
        )}
      </div>
    </div>
  );
}
